import React from 'react'
import millify from 'millify'
import { Col, Row, Typography, Statistic } from 'antd'
import { Link } from 'react-router-dom'
import { useGetCryptosQuery } from '../../services/cryptoApi'
import { Cryptocurrencies, News } from '..'
import style from './Homepage.module.scss'

const { Title } = Typography

const Homepage = () => {

  const { data, isFetching } = useGetCryptosQuery(10)
  const globalStats = data?.data?.stats

  if(isFetching) return 'Loading...'

  return (
    <>
      <Title level={2} className={style.heading}>
        Global Crypro Stats
      </Title>
      <Row>
        <Col span={12}>
          <Statistic title="Total Cryptocurrencies" value={globalStats.total} />
        </Col>
        <Col span={12}>
          <Statistic title="Total Exchanges" value={millify(globalStats.totalExchanges)} />
        </Col>
        <Col span={12}>
          <Statistic title="Total Market Cup" value={millify(globalStats.totalMarketCap)} />
        </Col>
        <Col span={12}>
          <Statistic title="Total 24h Volume" value={millify(globalStats.total24hVolume)} />
        </Col>
        <Col span={12}>
          <Statistic title="Total Markets" value={millify(globalStats.totalMarkets)} />
        </Col>
      </Row>
      <div className={style.home_heading_container}>
        <Title level={2} className={style.home_title}>Top 10 Cryptocurrencies in the world</Title>
        <Title level={3} className={style.show_more}><Link to='/cryptocurrencies'>Show more</Link></Title>
      </div>
      <Cryptocurrencies simplified />
      <div className={style.home_heading_container}>
        <Title level={2} className={style.home_title}>Latest Crypto News</Title>
        <Title level={3} className={style.show_more}><Link to='/news'>Show more</Link></Title>
      </div>
      <News simplified />
    </>
  )
}

export default Homepage
